import React from 'react';
import PropTypes from 'prop-types';
// import csx from 'classnames';
import css from './Settings.css';

export default class Settings extends React.PureComponent {
  static propTypes = {
    settings: PropTypes.object,
    onChange: PropTypes.func
  };

  constructor (props) {
    super(props);
    this.state = {};
  }

  onChange = e => {
    const { settings } = this.props;
    const newSettings = { ...settings };
    newSettings[e.target.name] = e.target.checked;
    this.props.onChange(newSettings);
  };

  renderSetting ({ label, id, help }) {
    const { settings } = this.props;
    return (
      <p className={css.row}>
        <label htmlFor={id}>{label}</label>
        <input
          type="checkbox"
          id={id}
          name={id}
          checked={!!settings[id]}
          onChange={this.onChange}
          />
        {help ? <span className={css.help}>{help}</span> : null}
      </p>
    );
  }

  render () {
    return (
      <form className={css.settings}>
        <h2>Stillingar</h2>
        {this.renderSetting({
          label: 'Erfiðari viðureign',
          id: 'hardMode',
          help: 'Þekktir stafir verða að vera notaðir í eftirfarandi ágiskunum.'
        })}
        {this.renderSetting({
          label: 'Gefa 6 stafi',
          id: 'handicap6',
          help: 'Leikurinn byrjar með 6 ónotað stafi sýnilega.'
        })}
        {this.renderSetting({
          label: 'Hefðbundið lyklaborð',
          id: 'qwerty',
          help: 'Nota „ERTY...“ uppröðun á lyklaborði frekar en „AÁBD...“'
        })}
      </form>
    );
  }
}
